html,body{height:100%;min-width:1000px}.chardinjs-top .chardinjs-tooltiptext,.chardinjs-bottom .chardinjs-tooltiptext{text-align:center}#progress-bar{overflow:visible;z-index:2}#progress-bar .bar{box-shadow:-3px -1px 6px 3px rgba(255,255,255,0.59)}#brand{margin-left:1rem}#brand img{position:relative;top:2px}#steps-previous{margin-right:1rem}#steps-next{min-width:220px;margin:0 1rem}#container{position:relative}#container header{display:-webkit-box;display:-ms-flexbox;display:flex;position:fixed;top:0;left:0;right:0;height:80px;z-index:0;background:#393F44}#container header .header-section{padding:0 1rem;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:distribute;justify-content:space-around}#container header #steps-navigation{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-flex:1;-ms-flex-positive:1;flex-grow:1}#container header #steps-navigation #steps-previous,#container header #steps-navigation #steps-next{border-bottom:1px solid rgba(34,36,38,0.14902)}#container header #steps-navigation #steps-breadcrumbs{-webkit-box-flex:1;-ms-flex-positive:1;flex-grow:1;border-left:1px solid #5c6873;border-right:1px solid #5c6873}#container header #steps-navigation #steps-breadcrumbs .steps{position:relative;top:-2px;border-left:0;border-right:0;border-bottom:1px solid #393F44;text-align:left}#container header #steps-navigation #steps-breadcrumbs .steps .step{height:80px;background:transparent;border-color:#5c6873}#container header #steps-navigation #steps-breadcrumbs .steps .step .title,#container header #steps-navigation #steps-breadcrumbs .steps .step .description,#container header #steps-navigation #steps-breadcrumbs .steps .step .icon{color:#dadada}#container header #steps-navigation #steps-breadcrumbs .steps .step i.icon{font-family:Roboto}#container header #steps-navigation #steps-breadcrumbs .steps .step::after{background:#393F44;border-color:#778694}#container header #steps-navigation #steps-breadcrumbs .steps .step.active::after{background:#778694}#container header #steps-navigation #steps-breadcrumbs .steps .step.active{background:#778694}#container header #steps-navigation #steps-breadcrumbs .steps .step.active .title,#container header #steps-navigation #steps-breadcrumbs .steps .step.active .description,#container header #steps-navigation #steps-breadcrumbs .steps .step.active .icon{color:white}#container main{position:fixed;top:80px;left:0;right:0;bottom:0;overflow:hidden;background:#5c6873}#cs-step-text{line-height:36px;font-size:20px}


